:root {
    --green: hsl(144, 60%, 36%);
    --red: hsl(357, 65%, 59%);
    --yellow: hsl(30, 57%, 55%);

    --dark: hsl(220, 30%, 12%);
    --primary: rgb(18 38 60);
    --light: hsl(213, 33%, 28%);
}

.AppHolder {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    width: 100vw;
}

.App {
    color: white;
    width: 70vw;
    height: 80vh;
    display: flex;
    flex-direction: column;
    pointer-events: all;
    background-color: var(--light);
    font-family: 'DM Mono';

    border-radius: 8px 8px 8px 8px;
}

.Topbar {
    width: 100%;
    height: 10vh;
    background-color: rgb(18 38 60);

    display: flex;
    justify-content: center;
    align-items: center;

    color: white;
    font-size: 30px;
    font-family: 'DM Mono', monospace;

    border-radius: 8px 8px 0px 0px;
    border-bottom: 1px solid blue;
    z-index: 2;
}

.Main {
    width: 100%;
    height: 70vh;
    display: flex;
}

.Sidebar {
    width: 20%;
    height: 100%;
    background-color: #151b27;
    overflow: auto;
}

.SidebarButton {
    cursor: pointer;
    padding: 16px;
}

.SidebarButton:hover {
    background-color: #30455f;
}

.Container {
    width: 85%;
    padding: 8px;
}

.Spinner {
    width: 100%;
    height: 100%;

    display: flex;
    justify-content: center;
    align-items: center;
}

/* 
  *PAGES
*/

.PageSectionContainer {
    width: 100%;
    height: 100%;

    display: flex;
    flex-direction: column;
    background-color: rgb(18 38 60);
}

.PageSectionPadding {
    padding: 4px;
}

.FactionInfoMain {
    width: 100%;
    height: 100%;

    display: flex;
}

.FactionInfoMainText {
    width: 100%;
    height: 100%;

    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

    color: white;
}

.FactionMembersMain {
    width: 100%;
    height: 100%;

    display: flex;
    flex-direction: column;
}

.SearchBar {
    width: 95%;
    height: 95%;

    display: flex;
    justify-content: space-between;
    align-items: center;
}

.Modal {
    overflow: auto;
    width: 100%;
    height: 100%;
    color: white;

    display: flex;
    align-items: center;
    justify-content: center;
}

.ModalContainer {
    display: flex;
    padding: 32px;
    box-sizing: border-box;
    flex-direction: column;
    background-color: #151b27;
    border-radius: 2px;
    border: 8px solid #30455f;

    width: 25vw;
}

.ModalTopbar {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

/* width */
::-webkit-scrollbar {
    width: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
    border-radius: 10px;
    box-shadow: inset 0 0 3px rgba(0, 0, 0, 0.205);

    background: hsla(334, 33%, 28%, 0.822);
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: hsl(334, 33%, 28%);
}
